import { Component, OnInit } from '@angular/core';
import {ProfTitle} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-prof-title',
  templateUrl: './prof-title.component.html',
  styleUrls: ['./prof-title.component.css']
})
export class ProfTitleComponent implements OnInit {

  profTitles: ProfTitle[];
  constructor(private schoolService: UniversityService) { }

  ngOnInit() {
    this.getprofTitles();
  }
  getprofTitles(): void {
    this.schoolService.getProfTitles()
      .subscribe(res => this.profTitles = res);
  }
  delete(profTitle: ProfTitle | number): void {
    this.profTitles = this.profTitles.filter(h => h !== profTitle);
    this.schoolService.deleteProfTitle(profTitle).subscribe(res => alert(res.message));
  }
}
