import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddCollegeComponent } from './school/add-college.component';
import { UpdateCollegeComponent } from './school/update-college.component';
import { CollegeComponent } from './school/college.component';
import { TeacherComponent } from './teacher/teacher.component';
import {DepartmentComponent} from './department/department.component';
import {AddDepComponent } from './department/add-dep.component';
import {UpdateDepComponent } from './department/update-dep.component';
import {AddTeacherComponent} from './teacher/add-teacher.component';
import {DegreeComponent} from './degree/degree.component';
import {AddComponent} from './degree/add.component';
import {UpdateComponent} from './degree/update.component';
import {UpdateTeacherComponent} from './teacher/update-teacher.component';
import {ProfTitleComponent} from './profTitle/prof-title.component';
import {AddProfTitleComponent} from './profTitle/add-prof-title.component';
import {UpdateProfTitleComponent} from './profTitle/update-prof-title.component';
const routes: Routes = [
  /*-----------------------college-------------------------------------------*/
  { path: '', redirectTo: '/college', pathMatch: 'full' },
  { path: 'college', component: CollegeComponent},
  { path: 'addCollege', component: AddCollegeComponent},
  { path: 'updateCollege/:id', component: UpdateCollegeComponent},
  /*--------------------------department--------------------------------------*/
  { path: 'addDep', component: AddDepComponent},
  { path: 'department', component: DepartmentComponent},
  { path: 'updateDep/:id', component: UpdateDepComponent},
  /*---------------------------teacher---------------------------------------*/
  { path: 'teacher', component: TeacherComponent},
  { path: 'addTeacher', component: AddTeacherComponent},
  { path: 'updateTeacher', component: UpdateTeacherComponent},
  /*---------------------------degree----------------------------------------*/
  { path: 'degree', component: DegreeComponent},
  { path: 'addDegree', component: AddComponent},
  { path: 'updateDegree', component: UpdateComponent},
  /*-----------------------------profTitle-----------------------------------------*/
  { path: 'profTitle', component: ProfTitleComponent},
  { path: 'addProfTitle', component: AddProfTitleComponent},
  { path: 'updateProfTitle', component: UpdateProfTitleComponent},
  /*-----------------------------------------------------------------------------*/
];
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
