import {Component, Input, OnInit} from '@angular/core';
import {College, Degree, Department, ProfTitle, Teacher} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add-teacher',
  templateUrl: './add-teacher.component.html',
  styleUrls: ['./add-teacher.component.css']
})
export class AddTeacherComponent implements OnInit {

  @Input() departments: Department[];
  @Input() degrees: Degree[];
  @Input() profTitles: ProfTitle[];
  department: Department; // 所属学院
  teacher: Teacher; // 所属职位
  degree: Degree; // 所属学位
  profTitle: ProfTitle;
  constructor(
    private route: ActivatedRoute,
    private teacherService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getProfTitle();
    this.getDepartment();
    this.getDegree();
  }
  getDepartment(): void {
    this.teacherService.getDepartments().subscribe(res => this.departments = res);
  }
  getProfTitle(): void {
    this.teacherService.getProfTitles().subscribe(res => this.profTitles = res);
  }
  getDegree(): void {
    this.teacherService.getDegrees().subscribe(res => this.degrees = res);
  }

  // save(name: string, no: string, department: Department, profTitle: ProfTitle, degree: Degree): void {
  //   this.teacher = {name: name.trim(), no: no.trim(), department: this.department, profTitle: this.profTitle, degree: this.degree };
  //   if (!description) { return; }
  //   this.teacherService.addTeacher(this.teacher)
  //     .subscribe(() => this.goBack());
  // }

  goBack(): void {
    this.location.back();
  }

}
