import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {

  @Input() degree: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private degreeService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
  }
  save(description: string, no: string, remarks: string): void {
    this.degree = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.degreeService.addDegree(this.degree)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}
